
.typography{
 padding: 10px 0 40px;  

    h1,h2, h3, h4,h5,h6{     
     color: #262626;
     }
    .typography__title{
     text-align: center;
     margin: 10px 0 44px;
    }
    .typography__title44{
        font-size: 44px;
        margin: 48px 0 32px;
    }
    .typography__title34{
        font-size: 34px;
        margin: 44px 0 29px;
    }
    .typography__title24{
        font-size: 24px;
        margin: 40px 0 26px;
    }
    .typography__title20{
        font-size: 20px;
        margin: 36px 0 21px;
    }
    .typography__title17{
        font-size: 17px;
        margin: 31px 0 17px;
    }
    .typography__title13{
        font-size: 13px;
        margin:25px 0 13px ;
    }
    .blok{
        max-width: 70%;
        margin: 0px auto;
        text-align: justify;
    }
    a{
        border-bottom: 2px solid #7d7d7dc7;
        font-weight: 500;
    }
    .blok-line{
        border-bottom: 4px double $green; 
        padding-bottom: 40px; 
    }
}

.withimg{
   padding: 10px 0; 
   .blok{
    max-width: 70%;
    margin: 0px auto;
    text-align: justify;
    border-bottom: 4px double $green; 
    padding-bottom: 40px; 
    h2{
        color: #262626;
        margin: 10px 0 29px;
    }
    img{
        width: 315px;
        margin: 5px auto;
    display: block;
    }
 
    .line { 
        border-left: 2px solid #ccc; /* Параметры линии */ 
        margin-left: 50px; /* Отступ слева */
        padding-left: 30px; /* Расстояние от линии до текста */ 
        margin-bottom: 15px;
        .line__text{
            font-size: 19px;
            color: #1f3315;
            font-family: 'Playfair Display', serif;
            margin: 25px auto;
       
        }
    }
    .quote{
        max-width: 617px;
        background: #3f5b2c;
        padding: 20px;
        margin: 30px auto;
        
        .quote__img{
            width: 27px;
            align-self: end;
            margin: 5px 30px 30px 16px;
        }
        .quote__line{
            margin: 0 10px 0 0;
            border-left: 0.5px solid #ffffff1f;

            blockquote{
                color: $white;
                font-size: 19px;
                font-family: 'Playfair Display', serif;
            }
            p{
                color: $white;  
                margin: 20px 1.5em 15px;
                opacity: 0.8;
            }
        }
    }
}
}
.table{
    .blok{
        max-width: 70%;
        margin: 0px auto;
        text-align: justify;
       
        padding-bottom: 40px; 
        h2{
            color: #262626;
            margin: 10px 0 29px;
        }
        table td, table th{
            border: 1px solid $green ;
            font-size: 14px;
            padding: 27px 5px 28px 20px;
        }
    }
}